

















import { Vue, Component } from 'vue-property-decorator';
import { marketRecentDeal } from '@/utils/restful.ts';
import DealItem from './DealItem.vue';

@Component({
  components: {
    DealItem,
  },
})
export default class extends Vue {
  dealData: any[] = [];

  mounted() {
    this.getDealData();
  }
  async getDealData() {
    try {
      const res = await marketRecentDeal({
        marketId: 1,
      });
      this.dealData = res;
    } catch (err) {
      console.log(err);
    }
  }
}
